html, body, main, #root {
  min-height: 100vh; }

body {
  font-family: Poppins, "Helvetica Neue", Arial, Helvetica, sans-serif;
  font-weight: lighter; }

h1, h2, h3, h4, h5, h6 {
  margin: 10px 0;
  font-weight: 300; }

a {
  text-decoration: none;
  color: inherit; }

.group-list {
  background: white;
  border-radius: 0 20px 20px 0;
  padding: 10px;
  height: 100%;
  overflow: auto;
  display: grid;
  grid-gap: 20px;
  grid-template-rows: auto 1fr; }
  .group-list .group-list__buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    margin-top: 10px; }
  .group-list .group-list__items {
    display: grid;
    grid-template-columns: 1fr;
    padding: 10px 0;
    overflow: auto;
    align-content: start; }
  .group-list .group-list__item {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    border-bottom: 1px solid #ECEFF2; }
    .group-list .group-list__item:last-child {
      border-bottom: 0; }
    .group-list .group-list__item:hover, .group-list .group-list__item.selected {
      background: #ECEFF2; }
    .group-list .group-list__item.selected {
      font-weight: bold; }

.task-list {
  color: white;
  padding: 20px;
  height: 100%;
  overflow: auto;
  display: grid;
  grid-gap: 20px;
  grid-template-rows: auto 1fr;
  min-width: 400px; }
  .task-list .task-list__buttons {
    display: grid;
    grid-template-columns: 1fr 140px 140px;
    grid-gap: 20px;
    margin-top: 10px; }
  .task-list .task-list__items {
    display: grid;
    grid-template-columns: 1fr;
    align-content: start;
    align-items: start;
    grid-gap: 20px;
    padding: 10px 0;
    overflow: auto; }
  .task-list .task-list__item {
    padding: 10px;
    background: white;
    border-radius: 20px;
    border: 1px dotted transparent;
    color: #5A6978;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
    .task-list .task-list__item.done {
      text-decoration: line-through;
      background: #b5f8d3;
      border: 1px solid #13CE66; }

.todolist-app {
  display: grid;
  grid-template-columns: 300px 1fr;
  width: 100%;
  height: 100vh;
  background: #365E80;
  box-shadow: 0 0 15px 0 #5A6978; }
  @media all and (min-width: 1024px) {
    .todolist-app {
      width: 80%; } }

.main-title {
  border-bottom: 1px solid #1DC1C1; }

.description {
  font-size: 0.8em; }

.input {
  padding: 10px;
  border: 0;
  border-bottom: 1px solid;
  outline: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  width: 100%;
  background: transparent;
  color: inherit;
  opacity: 0.5; }
  .input:hover, .input:focus {
    opacity: 1; }

.button {
  padding: 10px;
  border: 1px solid;
  color: #13CE66;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  outline: 0;
  cursor: pointer;
  background: #e4fcef; }
  .button.disabled {
    color: #ECEFF2;
    cursor: not-allowed;
    background: transparent; }
  .button:not(.disabled).danger {
    color: #F95F62;
    background: #fff2f3; }
  .button:not(.disabled):hover {
    -webkit-transform: scale(0.95);
            transform: scale(0.95); }
  .button:not(.disabled):active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

